.overlay--dark {
  overflow: hidden;
}

.overlay--dark::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
  height: 100%;
  width: 100%;
  z-index: 0;
  mix-blend-mode: multiply;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  overflow: auto;
}

body {
  font-family: "GT Walsheim", Arial, Helvetica, sans-serif;
  font-size: var(--body-font-size, 16px);
  font-weight: 300;
  background: var(--c-white, white);
  color: var(--c-black, black);
  overflow-x: hidden;
}

body::before {
  content: "";
  display: block;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  background: white;
  z-index: 99;
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
  transition: all 0.25s cubic-bezier(0.86, 0, 0.07, 1);
}

body::after {
  content: "";
  display: block;
  width: 1px;
  height: 100vh;
  background-color: #ddd;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: -1;
}

body a {
  text-decoration: none;
  transition: all 0.25s cubic-bezier(0.86, 0, 0.07, 1);
  color: inherit;
}

body a svg path {
  fill: currentColor;
}

img,
video,
object,
svg,
iframe {
  max-width: 100%;
  border: none;
  display: block;
}

strong,
b {
  font-weight: 500;
}

