/**
 * Wrapping element to keep content contained and centered.
 */

.l-wrap {
  padding-left: var(--space);
  padding-right: var(--space);
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

/**
 * Layout containers - keep content centered and within a maximum width. Also
 * adjusts left and right padding as the viewport widens.
 */

.l-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  max-width: 1200px;
}

.l-container--s {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  max-width: 600px;
}

.l-container--m {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  max-width: 728px;
}

.l-container--l {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  max-width: 970px;
}

.l-container--xl {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  max-width: 1200px;
}

.l-container--xxl {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  max-width: 1440px;
}

.l-container--full {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}

.l-container--gradient-white {
  width: 100vw;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  background: linear-gradient(var(--c-white, white), rgba(255, 255, 255, 0));
  padding-top: calc(var(--space) * 2);
  padding-bottom: 0;
  padding-left: 0;
  padding-right: 0;
}

@media (min-width: 721px) {
  .l-container--gradient-white {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    padding-left: var(--space);
    padding-right: var(--space);
  }
}

@media (min-width: 961px) {
  .l-container--gradient-white {
    margin-left: auto;
    margin-right: auto;
    overflow: inherit;
    padding-left: calc(var(--space) * 2);
    padding-right: calc(var(--space) * 2);
  }
}

.l-container--with-border {
  border-top: 3px solid var(--c-primary, black);
}

.o-heading + .l-container--with-border {
  margin-top: -9px;
}

@media (min-width: 721px) {
  .o-heading + .l-container--with-border {
    margin-top: -12px;
  }
}

@media (min-width: 961px) {
  .o-heading + .l-container--with-border {
    margin-top: -16px;
  }
}

.o-heading__secondary--xxl + .l-container--with-border,
.c-cards-featured__header + .l-container--with-border {
  margin-top: -5px;
