/* ------------------------------------ *\
    $SLICK
\* ------------------------------------ */

.slick-slider {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.slick-slider .slick-list {
  width: 100%;
  height: auto;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.slick-slider .slick-track {
  display: flex;
  width: 100%;
}

.slick-slider .slick-slide {
  width: 100%;
  margin: 0 calc(30px / 2);
}

.slick-slider .slick-arrow {
  padding: 0;
  display: block;
  cursor: pointer;
  min-width: 50px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-indent: 9999px;
  overflow: hidden;
  transition: all 0.25s cubic-bezier(0.86, 0, 0.07, 1);
  position: relative;
  background: transparent;
  border: 0;
  outline: 0;
}

@media (min-width: 1201px) {
  .slick-slider .slick-arrow {
    line-height: 65px;
    min-width: 65px;
    width: 65px;
    height: 65px;
  }
}

.slick-slider .slick-arrow:hover {
  background-color: var(--c-primary, black);
}

.slick-slider .slick-arrow.slick-disabled {
  opacity: 0.4;
  pointer-events: none;
}

.slick-slider .slick-arrow::before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  border-left: 3px solid var(--c-black, black);
  border-bottom: 3px solid var(--c-black, black);
  position: absolute;
  top: 50%;
  left: calc(50% + 3px);
  transform: translate(-50%, -50%) rotate(45deg);
}

@media (min-width: 1201px) {
  .slick-slider .slick-arrow::before {
    width: 25px;
    height: 25px;
    left: calc(50% + 5px);
  }
}

.slick-slider .slick-dots {
  position: absolute;
  left: 0;
  right: 0;
  bottom: calc(var(--space) / 2);
  margin: auto;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 100%;
  height: auto;
  overflow: hidden;
}

.slick-slider .slick-dots .slick-active button {
  border-color: var(--c-secondary, black);
}

.slick-slider .slick-dots li {
  list-style-type: none;
}

.slick-slider .slick-dots button {
  text-indent: 9999px;
  height: var(--space);
  width: var(--space);
  border-radius: var(--space);
  background-color: transparent;
  border: 1px solid transparent;
  margin: calc(var(--space) / 4);
  text-align: center;
  position: relative;
  padding: 0;
  cursor: pointer;
}

.slick-slider .slick-dots button::after {
  content: "";
  display: block;
  height: 6px;
  width: 6px;
  border-radius: 6px;
  background-color: var(--c-secondary, black);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

.slick-slider .slick-next::before {
  left: calc(50% - 3px);
  transform: translate(-50%, -50%) rotate(-135deg);
}

@media (min-width: 1201px) {
  .slick-slider .slick-next::before {
    left: calc(50% - 5px);
  }
}

/* ------------------------------------ *\
    $MAGNIFIC POPUP
\* ------------------------------------ */

.mfp-bg {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999998;
  overflow: hidden;
  position: fixed;
  background: rgba(0, 0, 0, 0.8);
  mix-blend-mode: multiply;
}

.mfp-wrap {
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh !important;
  z-index: 999999;
  position: fixed !important;
  outline: none !important;
  -webkit-backface-visibility: hidden;
}

.mfp-container {
  text-align: center;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  padding: 0 var(--space);
  box-sizing: border-box;
}

.mfp-container::before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.mfp-align-top .mfp-container::before {
  display: none;
}

.mfp-content {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
  text-align: left;
  z-index: 1045;
}

.mfp-inline-holder .mfp-content,
.mfp-ajax-holder .mfp-content {
  width: 100%;
  cursor: auto;
}

.mfp-ajax-cur {
  cursor: progress;
}

.mfp-zoom-out-cur,
.mfp-zoom-out-cur .mfp-image-holder .mfp-close {
  cursor: -moz-zoom-out;
  cursor: -webkit-zoom-out;
  cursor: zoom-out;
}

.mfp-zoom {
  cursor: -webkit-zoom-in;
  cursor: -moz-zoom-in;
  cursor: zoom-in;
}

.mfp-auto-cursor .mfp-content {
  cursor: auto;
}

.mfp-close,
.mfp-arrow,
.mfp-preloader,
.mfp-counter {
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.mfp-loading.mfp-figure {
  display: none;
}

.mfp-hide {
  display: none !important;
}

.mfp-preloader {
  color: white;
  position: absolute;
  top: 50%;
  width: auto;
  text-align: center;
  margin-top: -0.8em;
  left: 8px;
  right: 8px;
  z-index: 1044;
}

.mfp-preloader a {
  color: white;
}

.mfp-preloader a:hover {
  color: white;
}

.mfp-s-ready .mfp-preloader {
  display: none;
}

.mfp-s-error .mfp-content {
  display: none;
}

button.mfp-close,
button.mfp-arrow {
  overflow: visible;
  cursor: pointer;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
  display: block;
  outline: none;
  padding: 0;
  z-index: 1046;
  box-shadow: none;
  touch-action: manipulation;
  font-weight: normal;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.mfp-close {
  width: 44px;
  height: 44px;
  line-height: 44px;
  position: fixed;
  right: 0;
  top: 0;
  text-decoration: none;
  text-align: center;
  opacity: 0.65;
  padding: 0 0 var(--space) var(--space);
  color: white;
  font-style: normal;
  font-size: 30px;
  font-family: inherit;
}

.mfp-close:hover,
.mfp-close:focus {
  opacity: 1;
}

.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  color: white;
  right: 0;
  text-align: right;
  width: 100%;
}

.mfp-counter {
  position: absolute;
  top: 0;
  right: 0;
  color: white;
  font-size: 12px;
  line-height: 18px;
  white-space: nowrap;
}

.mfp-arrow {
  position: absolute;
  opacity: 0.65;
  margin: 0;
  padding: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100px;
  height: 100px;
  -webkit-tap-highlight-color: transparent;
}

.mfp-arrow:hover,
.mfp-arrow:focus {
  opacity: 1;
}

.mfp-arrow::after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  border: medium inset transparent;
  border-top-width: 15px;
  border-bottom-width: 15px;
}

.mfp-arrow.mfp-arrow-left {
  left: 0;
}

.mfp-arrow.mfp-arrow-left::after {
  left: 30px;
  border-right: 15px solid white;
}

.mfp-arrow.mfp-arrow-right {
  right: 0;
}

.mfp-arrow.mfp-arrow-right::after {
  right: 30px;
  border-left: 15px solid white;
}

.mfp-iframe-holder {
  padding-top: calc(var(--space) * 2);
  padding-bottom: calc(var(--space) * 2);
}

.mfp-iframe-holder .mfp-content {
  line-height: 0;
  width: 100%;
  max-width: 900px;
}

.mfp-iframe-holder .mfp-close {
  top: -40px;
}

.mfp-iframe-scaler {
  width: 100%;
  height: 0;
  overflow: hidden;
  padding-top: 56.25%;
}

.mfp-iframe-scaler iframe {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  background: #000;
}

/* Main image in popup */

img.mfp-img {
  width: auto;
  max-width: 100%;
  height: auto;
  display: block;
  line-height: 0;
  box-sizing: border-box;
  padding: calc(var(--space) * 2) 0 calc(var(--space) * 2);
  margin: 0 auto;
}

/* The shadow behind the image */

.mfp-figure {
  line-height: 0;
}

.mfp-figure::after {
  content: '';
  position: absolute;
  left: 0;
  top: 40px;
  bottom: 40px;
  display: block;
  right: 0;
  width: auto;
  height: auto;
  z-index: -1;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  background: #444;
}

.mfp-figure small {
  color: #ddd;
  display: block;
  font-size: 12px;
  line-height: 14px;
}

.mfp-figure figure {
  margin: 0;
}

.mfp-bottom-bar {
  margin-top: -36px;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  cursor: auto;
}

.mfp-title {
  text-align: left;
  line-height: 18px;
  color: white;
  word-wrap: break-word;
  padding-right: calc(var(--space) * 2);
}

.mfp-image-holder .mfp-content {
  max-width: 100%;
}

.mfp-gallery .mfp-image-holder .mfp-figure {
  cursor: pointer;
}

@media screen and (max-width: 800px) and (orientation: landscape), screen and (max-height: 300px) {
  /**
   * Remove all paddings around the image on small screen
   */

  .mfp-img-mobile .mfp-image-holder {
    padding-left: 0;
    padding-right: 0;
  }

  .mfp-img-mobile img.mfp-img {
    padding: 0;
  }

  .mfp-img-mobile .mfp-figure::after {
    top: 0;
    bottom: 0;
  }

  .mfp-img-mobile .mfp-figure small {
    display: inline;
    margin-left: 5px;
  }

  .mfp-img-mobile .mfp-bottom-bar {
    background: rgba(0, 0, 0, 0.6);
    bottom: 0;
    margin: 0;
    top: auto;
    padding: 3px 5px;
    position: fixed;
    box-sizing: border-box;
  }

  .mfp-img-mobile .mfp-bottom-bar:empty {
    padding: 0;
  }

  .mfp-img-mobile .mfp-counter {
    right: 5px;
    top: 3px;
  }

  .mfp-img-mobile .mfp-close {
    top: 0;
    right: 0;
    width: 35px;
    height: 35px;
    line-height: 35px;
    background: rgba(0, 0, 0, 0.6);
    position: fixed;
    text-align: center;
    padding: 0;
  }
}

@media (min-width: 961px) {
  .mfp-arrow {
    -webkit-transform: scale(0.75);
    transform: scale(0.75);
  }

  .mfp-arrow-left {
    -webkit-transform-origin: 0;
    transform-origin: 0;
  }

  .mfp-arrow-right {
    -webkit-transform-origin: 100%;
    transform-origin: 100%;
  }

  .mfp-container {
    padding-left: 6px;
    padding-right: 6px;
  }
}

