.o-ad {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.o-ad script {
  display: none;
}

.o-ad > * {
  margin: 0 auto !important;
  width: 100%;
  max-width: 970px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.o-ad--banner {
  width: 100vw;
  left: 50%;
  right: 50%;
  margin: calc(var(--space) * 2) -50vw var(--space);
  position: relative;
  overflow: hidden;
}

.o-ad--sidebar {
  margin-bottom: var(--space);
  display: none;
}

@media (min-width: 1601px) {
  .o-ad--sidebar {
    display: block;
    position: relative;
    z-index: 2;
    top: 0;
    float: right;
    width: 320px;
    margin: 0 calc(-320px - calc(var(--space) * 2)) 0 0;
    align-items: flex-start;
    justify-content: flex-start;
  }
}

@media (max-width: 720px) {
  .o-ad--step {
    width: 100vw;
    position: relative;
    left: 50%;
    right: 50%;
    margin-left: -50vw;
    margin-right: -50vw;
  }
}

.home .o-ad,
.woocommerce-page .o-ad {
  display: none;
  visibility: hidden;
  opacity: 0;
}

.o-button {
  border: none;
  cursor: pointer;
  outline: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  text-align: center;
  background-color: var(--c-primary, black);
  color: white;
  padding: 15px calc(var(--space) * 2);
  transition: all 0.25s cubic-bezier(0.86, 0, 0.07, 1);
  border-radius: 50px;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.15);
  font-family: var(--font-primary, "GT Walsheim", Arial, Helvetica, sans-serif);
  font-weight: 500;
  font-size: var(--font-size-s, 16px);
  line-height: 1;
}

.o-button:focus,
.o-button:hover {
  color: var(--c-black, black);
  background-color: var(--c-tertiary, black);
}

.o-button .o-icon {
  position: relative;
  top: 2px;
}

.o-button span + span {
  margin-left: calc(var(--space) / 2);
}

