/* Search Modal */

.c-search-modal {
  display: block;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: #ddd;
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  pointer-events: none;
  transition: opacity 0.25s cubic-bezier(0.86, 0, 0.07, 1), visibility 0.25s cubic-bezier(0.86, 0, 0.07, 1);
  padding-top: calc(var(--space) * 4);
}

.c-search-modal::after {
  content: "";
  display: block;
  z-index: -1;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background-position: right -10vw bottom -10vh;
  background-repeat: no-repeat;
  background-size: 100vw auto;
}

@media (min-width: 501px) {
  .c-search-modal::after {
    background-size: auto 80vh;
  }
}

.c-search-modal__close {
  padding: var(--space);
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  outline: 0;
  background: transparent;
  color: var(--c-black, black);
}

.c-search-modal__close .o-icon {
  transition: all 0.25s cubic-bezier(0.86, 0, 0.07, 1);
  transform: scale(1);
}

@media (max-width: 500px) {
  .c-search-modal__close .o-icon {
    width: 20px;
    height: 20px;
  }
}

.c-search-modal__close:hover,
.c-search-modal__close:focus {
  color: var(--c-black, black);
  background-color: transparent;
}

.c-search-modal__close:hover svg,
.c-search-modal__close:focus svg {
  transform: scale(1.1);
}

.c-search-modal__form {
  padding: 0 var(--space);
}

.c-search-modal.is-active {
  pointer-events: all;
  opacity: 1;
  visibility: visible;
  z-index: 99999;
}

body.search-is-active {
  overflow: hidden;
}

body.search-is-active .c-search-modal {
  pointer-events: all;
  opacity: 1;
  visibility: visible;
  z-index: 99999;
}

body.search-is-active .c-filter,
body.search-is-active .c-header {
  z-index: 1;
}

