  padding: calc(var(--space) * 2) var(--space) 50px var(--space);
  order: 1;
}

@media (min-width: 721px) {
  .c-promo__content {
    padding: calc(var(--space) * 2) calc(var(--space) * 4);
    order: 0;
    flex-basis: 50vw;
  }
}

.c-promo__body {
  margin-left: 0;
}

.c-promo__body > * {
  background-color: transparent !important;
}

.c-promo__image {
  min-height: 100vw;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  order: 0;
  position: relative;
}

@media (min-width: 721px) {
  .c-promo__image {
    min-height: 50vw;
    order: 1;
    flex-basis: 50vw;
  }
}

.c-promo__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  border-radius: 80px;
  background-color: var(--c-white, white);
  position: absolute;
  bottom: -40px;
  left: 0;
  right: 0;
  margin: auto;
}

@media (min-width: 721px) {
  .c-promo__icon {
    bottom: 0;
    top: 0;
    left: -40px;
    right: auto;
  }
}

.c-promo__icon img {
  width: 100%;
  height: auto;
  max-width: 40px;
  max-height: 40px;
}

.c-promo .slick-slide {
  margin: 0;
}

@media (min-width: 721px) {
  .c-promo .slick-dots {
    flex-direction: column;
    width: auto;
    height: 100%;
    bottom: 0;
    left: 20px;
  }
}

/*
 * General Layout
*/

.has-sidebar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
}

@media (min-width: 721px) {
  .has-sidebar {
    flex-wrap: nowrap;
  }
}

.has-sidebar > article {
  width: 100%;
  flex: 1;
}

@media (min-width: 721px) {
  .has-sidebar > article {
    width: calc(100% - (300px));
  }
}

.has-sidebar > aside {
  width: 100%;
  margin-top: calc(var(--space) * 2);
}

@media (min-width: 721px) {
  .has-sidebar > aside {
    margin-top: 0;
    margin-left: var(--space);
    min-width: 300px;
    max-width: 300px;
  }
}

@media (min-width: 961px) {
  .has-sidebar > aside {
    margin-left: calc(var(--space) * 2);
  }
}

/* ------------------------------------*\
    $PRINT STYLES
\*------------------------------------ */

/* stylelint-disable */

@media print {
  .u-print-only {
    display: block;
  }

@page {
    margin-top: 0.5in;
    margin-bottom: 0.5in;
    margin-left: 0.5in;
    margin-right: 0.5in;
}

  body {
    max-height: fit-content;
    background: white;
  }

  body::after {
    display: none;
  }

  main {
    overflow: hidden;
    background-color: var(--c-white, white);
  }

  .c-content-single-plans {
    background: white;
    margin-top: 0;
  }

  .c-content-single-plans__container > .c-page-header {
    margin-bottom: var(--space) !important;
  }

  nav {
    display: none;
  }

  .c-header {
    position: relative;
  }

  .c-header::before {
    background-color: var(--c-white, white);
  }

  .c-stats + picture {
    max-width: 50%;
    margin-left: auto;
    margin-right: auto;
  }

  .o-dek {
    font-family: "GT Walsheim", Arial, Helvetica, sans-serif;
    font-size: var(--body-font-size, 16px);
    font-weight: 300;
    line-height: 1.5;
    letter-spacing: 0.25px;
    max-width: 75%;
  }

  .o-link--small {
    text-decoration: none;
  }

  .o-link--small::after {
    content: ": " attr(href);
    color: var(--c-black, black);
    display: inline-flex;
    text-decoration: none;
    border: none;
  }

  .c-accordion__heading::before,
  .c-accordion__heading::after {
    display: none;
  }

  .c-intro + .c-content-single-plans__header,
  #material-list,
  #instructions {
    page-break-before: always;
  }

  .c-accordion__item,
  .o-figure {
    page-break-inside: avoid;
  }

  table {
    page-break-inside: avoid;
  }

  .c-steps__item {
    page-break-after: always;
  }

  .c-intro__signature {
    max-width: 2in !important;
  }

  .c-intro .o-icon {
    display: none;
  }
}

/* ------------------------------------*\
    $HELPER/TRUMP CLASSES
\*------------------------------------ */

#wpadminbar {
  z-index: 1;
}

.search-filter-scroll-loading {
  position: absolute;
  bottom: -2.5rem;
  left: 0;
  right: 0;
  width: 30px;
  margin: 0 auto;
  display: block;
}

/**
 * Completely remove from the flow but leave available to screen readers.
 */

.is-vishidden,
.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

.is-hidden {
  display: none !important;
}

/**
 * Hide elements only present and necessary for js enabled browsers.
 */

.no-js-hide {
  display: none;
}

.js-toggle,
.js-toggle-parent {
  cursor: pointer;
}

/**
 * Misc
 */

.u-color--yellow--with-shadow {
  text-shadow: 1px 1px 0 var(--c-tertiary, black);
  color: var(--c-primary, black);
}

.u-border--top--black {
  border-top: 3px solid var(--c-black, black);
}

.u-border--thick--white {
  border: 10px solid var(--c-white, white);
}

.u-border--med--white {
  border: 3px solid var(--c-white, white);
}

.u-hover--bw-to-color {
  filter: grayscale(100%);
  transition: all 0.5s ease-in-out;
}

.u-hover--bw-to-color:hover,
.u-hover--bw-to-color:focus {
  filter: grayscale(0);
}

.u-background-cover {
  background-size: cover;
  background-position: center center;
}

.u-gradient--top--white {
  background: linear-gradient(white, var(--c-white, white));
}

/**
 * Center block
 */

.u-text-align--center {
  text-align: center;
}

.u-center-block {
  margin-left: auto;
  margin-right: auto;
}

.u-reversed-out {
  color: var(--c-white, white);
}

.u-reversed-out p,
.u-reversed-out h1 a,
.u-reversed-out h2 a,
.u-reversed-out h3 a {
  color: var(--c-white, white);
}

.u-no-spacing {
  padding: 0;
  margin: 0;
}

.u-rounded {
  border-radius: 50%;
  overflow: hidden;
}

.u-text-transform--upper {
  text-transform: uppercase;
}




